import React from "react";
import { Routes, Route } from "react-router-dom";
import { LogIn } from "components/LogIn/LogIn";
import { Layout } from "components/Layout";
import RequireAuth from "components/RequireAuth/RequireAuth";
import { Unauthorized } from "components/Unauthorized";
import { Missing } from "components/Missing";
import { PersistLogin } from "components/PersistLogin";
import { Home } from "components/Home";
import { GosContracts } from "components/Pages/GettingBlocks/GosContracts";
import { Register } from "components/Register";
import { OrdersForm } from "components/OrdersForm";
import { OrdersTable } from "components/OrdersTable/OrdersTable";

import styles from "./App.module.css";
import { ResetPassword } from "components/ResetPassword/ResetPassword";
import { CreatePassword } from "components/CreatePassword";

const ROLES = {
  User: 1,
  Editor: 1984,
  Admin: 5150,
};

function App() {
  return (
    <>
      <div className={styles.background}> </div>
      <Routes>
        <Route path="/" element={<Layout />}>
          {/* public routes */}
          <Route path="login" element={<LogIn />}></Route>
          <Route path="register" element={<Register />}></Route>
          <Route path="unauthorized" element={<Unauthorized />}></Route>
          <Route path="resetpw" element={<ResetPassword />}></Route>
          <Route path="reset/:token" element={<CreatePassword />} />

          {/* protect routes */}
          <Route element={<PersistLogin />}>
            <Route
              element={<RequireAuth allowedRoles={[ROLES.Admin, ROLES.User]} />}
            >
              <Route path="/" element={<Home />} />
              <Route path="createorder" element={<OrdersForm />} />
              {/* <Route path="createorder2" element={<GosContracts />} /> */}
              <Route path="listoforders" element={<OrdersTable />} />
            </Route>

            <Route element={<RequireAuth allowedRoles={[ROLES.Editor]} />}>
              {/* Editor */}
            </Route>

            <Route element={<RequireAuth allowedRoles={[ROLES.Admin]} />}>
              {/* Admin */}
            </Route>

            <Route
              element={
                <RequireAuth allowedRoles={[ROLES.Editor, ROLES.Admin]} />
              }
            >
              {/* Lounge */}
            </Route>
          </Route>

          {/* catch all */}
          <Route path="*" element={<Missing />} />
        </Route>
      </Routes>
    </>
  );
}

export default App;
