import React, { useState, useEffect } from "react";
import axios from "api/axios";
import {
  TextField,
  Button,
  MenuItem,
  Select,
  Alert,
  Container,
  Typography,
} from "@mui/material";
import { NavBar } from "components/Pages/NavBar";
import { OptionsList } from "components/Helpers/OptionsList";

const OrdersForm = () => {
  const [selectedCode, setSelectedCode] = useState("");
  const [orderNumber, setOrderNumber] = useState("");
  const [price, setPrice] = useState("");
  const [error, setError] = useState(null);

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post(
        "api/createorderspromo",
        {
          code: selectedCode,
          orderNumber,
          price,
        },
        {
          headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
        }
      );
      console.log(response.data);
    } catch (err) {
      if (err.response && err.response.data) {
        setError(err.response.data.message);
      } else {
        setError("Failed to place order");
      }
    }
  };

  const pickedhandler = (event) => {
    console.log("OptionsListhandler", event.value.id);
    setSelectedCode(event.value.id);
  };

  return (
    <>
      <NavBar link={[]} name={"Заказы"} />

      <Container
        sx={{ marginTop: "25px", display: "flex", flexDirection: "column" }}
      >
        <Typography variant="h4" gutterBottom>
          Добавить заказ
        </Typography>

        <form onSubmit={handleSubmit}>
          <OptionsList
            required_field={true}
            name={"addr_id"}
            onPicked={pickedhandler}
            link={"api/codes"}
            defaultId={""}
            label={"Промокод"}
          />
          <TextField
            label="Номер Заказа"
            value={orderNumber}
            onChange={(e) => setOrderNumber(e.target.value)}
            fullWidth
            margin="normal"
            sx={{ width: 270 }}
          />
          <TextField
            label="Цена"
            type="number"
            value={price}
            onChange={(e) => setPrice(e.target.value)}
            fullWidth
            margin="normal"
            sx={{ width: 270 }}
          />
          {error && <Alert severity="error">{error}</Alert>}
          <Button
            sx={{ width: 270 }}
            type="submit"
            variant="contained"
            color="primary"
            fullWidth
          >
            Создать заказ
          </Button>
        </form>
      </Container>
    </>
  );
};

export { OrdersForm };
