import React, { useState } from "react";
import axios from "api/axios";
import {
  TextField,
  Button,
  Container,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
} from "@mui/material";
import { NavBar } from "components/Pages/NavBar";
import { OptionsList } from "components/Helpers/OptionsList";

const OrdersTable = () => {
  const [selectedCode, setSelectedCode] = useState("");
  const [orders, setOrders] = useState([]);

  const handleFetchOrders = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.get(`api/orderslist/${selectedCode}`);
      setOrders(response.data);
    } catch (error) {
      console.error("There was an error fetching the orders!", error);
    }
  };

  const pickedhandler = (event) => {
    console.log("OptionsListhandler", event.value);
    setSelectedCode(event.value.code);
  };

  return (
    <>
      <NavBar link={[]} name={"Заказы"} />
      <Container>
        {/* <input
        type="text"
        className={styles.searchBar}
        // onKeyPress={props.keypressing}
        value={currenElement}
        onChange={currentElementHandler}
        placeholder={props.placeholder}
        onKeyDown={getDaterHandler}
        required
      ></input> */}
        <Typography variant="h4" gutterBottom>
          Список заказов
        </Typography>
        <form onSubmit={handleFetchOrders}>
          <OptionsList
            required_field={true}
            name={"addr_id"}
            onPicked={pickedhandler}
            link={"api/codes"}
            defaultId={""}
            label={"Промокод"}
          />
          <Button       sx={{ mt: 2 }} type="submit" variant="contained" color="primary">
            Найти заказы
          </Button>
        </form>
        {orders.length > 0 && (
          <TableContainer component={Paper} style={{ marginTop: "20px" }}>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>Номер заказа</TableCell>
                  <TableCell>Цена</TableCell>
                  <TableCell>Дата</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {orders.map((order, id) => (
                  <TableRow key={id}>
                    <TableCell>{order.order_number}</TableCell>
                    <TableCell>{order.price}</TableCell>
                    <TableCell>{order.created_at}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        )}
      </Container>
    </>
  );
};

export { OrdersTable };
